    --col: min(calc((100% - (5 * var(--gap))) / 6),calc((100vh - 168px - (2 * var(--gap))) / 3));
  }

  .md\:\[grid-auto-rows\:0\] {
    grid-auto-rows: 0;
  }

  .md\:\[grid-rows\:repeat\(3\2c var\(--col\)\)\] {
    grid-rows: repeat(3,var(--col));
  }

  .md\:\[grid-template-rows\:repeat\(3\2c var\(--col\)\)\] {
    grid-template-rows: repeat(3,var(--col));
  }
}

@media (min-width: 1024px) {

  .lg\:mb-0 {
    margin-bottom: 0px;
  }

  .lg\:hidden {
    display: none;
  }

  .lg\:grid-cols-2 {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }

  .lg\:gap-x-32 {
    -moz-column-gap: 8rem;
         column-gap: 8rem;
  }

  .lg\:gap-x-8 {
    -moz-column-gap: 2rem;
         column-gap: 2rem;
  }

  .lg\:px-8 {
    padding-left: 2rem;
    padding-right: 2rem;
  }

  .lg\:text-5xl {
    font-size: 2.5rem;
  }

  .lg\:text-8xl {
    font-size: 6.25rem;
  }
}